
import UnitBase from "../../Base/UnitBase";
import { UnitCategory, UnitKind } from "../../Base/UnitDefines";
import FrameStateNormal from "./State/FrameStateNormal";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FrameBase extends UnitBase {

    protected initFrame(kind: UnitKind) {
        super.initUnit(UnitCategory.Frame, kind);
        this.setCurrentState(FrameStateNormal.create());
    }
}
