
import { UnitKind } from "../Base/UnitDefines";
import FrameBase from "./Base/FrameBase";
import FrameNormal from "./Normal/FrameNormal";

export default class FrameFactory {

    public static createFrame(param: { [key: string]: any }): FrameBase {
        let kind = param["kind"];
        switch (kind) {
            case UnitKind.FrameNormal: {
                return FrameNormal.create();
            }
                break;
            default:
                break;
        }
        return null
    }
}
