
import UnitStateBase, { UnitStateType } from "../../../Base/State/UnitStateBase";
import CandyBase from "../CandyBase";
import CandyStateNormal from "./CandyStateNormal";




export default class CandyStateBorn extends UnitStateBase {

    public static create(): CandyStateBorn {
        let instance = new CandyStateBorn();
        instance.init();
        return instance;
    }

    private _pastDuration: number = 0;
    protected init() {
        super.init(UnitStateType.Born);
    }

    public enter(onwer: CandyBase): void {
        onwer.node.scale = 0.0;
    }

    public update(onwer: CandyBase, dt: number): void {
        this._pastDuration += dt * 4;
        onwer.node.scale = Math.min(this._pastDuration, 1.0);
        if (this._pastDuration >= 1.0) {
            onwer.setCurrentState(CandyStateNormal.create());
        }
    }
}
