
import GridLayerBase, { LayerName } from "../Base/GridLayerBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CandyLayer extends GridLayerBase {

    public static create(contentSize: cc.Size, gridSize: cc.Size): CandyLayer {
        let node = new cc.Node();
        let layer = node.addComponent(CandyLayer);
        layer.initWithSize(contentSize, gridSize);
        return layer;
    }

    protected initWithSize(contentSize: cc.Size, gridSize: cc.Size) {
        super.init(LayerName.Candy, contentSize, gridSize);
    }
}
