import { AssetBundleID } from "../../../../../scripts/Defines/Defines";
import AssetCache from "../../../../../scripts/Frameworks/AssetCache/AssetCache";

export default class DropSceneAssetCache {
    public static getAssetSync(url: string, type: typeof cc.Asset): cc.Asset {
        return AssetCache.getInstance().getAssetSync(AssetBundleID.DropScene, url, type)
    }

    public static getSpriteFrame(url: string): cc.SpriteFrame {
        return this.getAssetSync(url, cc.SpriteFrame) as cc.SpriteFrame
    }

    public static getPrefab(url: string): cc.Prefab {
        return this.getAssetSync(url, cc.Prefab) as cc.Prefab
    }

    public static getKeyWithLogicalPosition(pos: cc.Vec2): number {
        return Math.floor(pos.y) * 100 + Math.floor(pos.x) + 10000;
    }
}
