import Grid from "../../Grid/Grid";
import GridLayerBase from "../../Layer/Base/GridLayerBase";
import UnitStateBase, { UnitStateType } from "./State/UnitStateBase";
import UnitStateMachine from "./State/UnitStateMachine";
import UnitStateNull from "./State/UnitStateNull";
import { UnitCategory, UnitColor, UnitKind } from "./UnitDefines";


const { ccclass, property } = cc._decorator;

@ccclass
export default abstract class UnitBase extends cc.Component {




    private _unitCategory: UnitCategory = UnitCategory.None;
    public getUnitCategory(): UnitCategory {
        return this._unitCategory;
    }
    protected setUnitCategory(category: UnitCategory) {
        this._unitCategory = category;
    }


    private _unitKind: UnitKind = UnitKind.None;
    public getUnitKind(): UnitKind {
        return this._unitKind;
    }
    protected setUnitKind(kind: UnitKind) {
        this._unitKind = kind;
    }

    private _healthPoint: number = 1;
    public getHP(): number {
        return this._healthPoint;
    }
    public setHp(hp: number) {
        this._healthPoint = hp;
    }

    private _unitColor: UnitColor = UnitColor.None;
    public getColor(): UnitColor {
        return this._unitColor;
    }
    public setColor(color: UnitColor) {
        this._unitColor = color;
    }

    private _layer: GridLayerBase = null;
    public get layer(): GridLayerBase {
        return this._layer;
    }
    public set layer(layer: GridLayerBase) {
        this._layer = layer;
    }

    public get grid(): Grid {
        return this._layer.grid;
    }
    private _logicalPostion: cc.Vec2 = cc.v2(0, 0);
    public getLogicalPostion(): cc.Vec2 {
        return this._logicalPostion;
    }
    public setLogicalPostion(pos: cc.Vec2) {
        this._logicalPostion = pos;
    }

    public getPixelPosition(): cc.Vec2 {
        return this.node.getPosition();
    }
    public setPixelPosition(pos: cc.Vec2) {
        this.node.setPosition(pos);
    }

    private _stateMachine: UnitStateMachine = null;
    public getStateMachine(): UnitStateMachine {
        return this._stateMachine;
    }

    public getCurrentState(): UnitStateBase {
        return this._stateMachine.getCurrentState();
    }

    public setCurrentState(state: UnitStateBase) {
        this._stateMachine.setCurrentState(state);
    }

    protected initUnit(category: UnitCategory, kind: UnitKind) {
        this.setUnitCategory(category);
        this.setUnitKind(kind);
        this._stateMachine = UnitStateMachine.create(this)
        this.setCurrentState(UnitStateNull.create());
    }

    public updateUnit(dt: number) {
        this._stateMachine.update(dt);
    }

}
