import UnitStateBase, { UnitStateType } from "../../../Base/State/UnitStateBase";
import CandyBase from "../CandyBase";
import CandyStateNormal from "./CandyStateNormal";




export default class CandyStateDrop extends UnitStateBase {

    public static create(targetPixelPosition: cc.Vec2): CandyStateDrop {
        let instance = new CandyStateDrop();
        instance.initWithTargetPos(targetPixelPosition);
        return instance;
    }

    private _pastDuration: number = 0;
    private _startPixelPosition: cc.Vec2 = cc.v2(0, 0);
    private _targetPixelPosition: cc.Vec2 = cc.v2(0, 0);

    protected initWithTargetPos(pos: cc.Vec2) {
        super.init(UnitStateType.Drop);
        this._targetPixelPosition = pos;
    }

    public enter(onwer: CandyBase): void {
        onwer.node.scale = 1.0;
        this._startPixelPosition = onwer.getPixelPosition();
    }

    public update(onwer: CandyBase, dt: number): void {
        this._pastDuration += dt * 4;
        onwer.setPixelPosition(this._startPixelPosition.lerp(this._targetPixelPosition, Math.min(1.0, this._pastDuration)))
        if (this._pastDuration >= 1.0) {
            onwer.setCurrentState(CandyStateNormal.create());
        }
    }
}
