import { UnitKind } from "../Base/UnitDefines";
import FloorBase from "./Base/FloorBase";
import FloorEntrance from "./Entrance/FloorEntrance";
import FloorExit from "./Exit/FloorExit";
import FloorFixed from "./Fixed/FloorFixed";


export default class FloorFactory {

    public static createFloor(param: { [key: string]: any }): FloorBase {
        let kind = param["kind"];
        switch (kind) {
            case UnitKind.FloorFixed: {
                return FloorFixed.create();
            }
                break;
            case UnitKind.FloorEntrance: {
                let exit = param["relatedPosition"] || cc.v2(-1, -1);
                return FloorEntrance.create(exit);
            }
                break;
            case UnitKind.FloorExit: {
                let entrance = param["relatedPosition"] || cc.v2(-1, -1);
                return FloorExit.create(entrance);
            }
                break;
            default:
                break;
        }
        return null;
    }

}
