
import GridLayerBase, { LayerName } from "../Base/GridLayerBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FrameLayer extends GridLayerBase {

    public static create(contentSize: cc.Size, gridSize: cc.Size): FrameLayer {
        let node = new cc.Node();
        let layer = node.addComponent(FrameLayer);
        layer.initWithSize(contentSize, gridSize);
        return layer;
    }

    protected initWithSize(contentSize: cc.Size, gridSize: cc.Size) {
        super.init(LayerName.Frame, contentSize, gridSize);
    }
}
