
import UnitBase from "../../Base/UnitBase";
import { UnitCategory, UnitKind } from "../../Base/UnitDefines";
import MarkStateNormal from "./State/MarkStateNormal";

const { ccclass, property } = cc._decorator;

@ccclass
export default class MarkBase extends UnitBase {
    protected initMark(kind: UnitKind) {
        super.initUnit(UnitCategory.Mark, kind);
        this.setCurrentState(MarkStateNormal.create());
    }
}
