
import GridLayerBase, { LayerName } from "../Layer/Base/GridLayerBase";
import UnitBase from "../Unit/Base/UnitBase";
import CandyBase from "../Unit/Candy/Base/CandyBase";
import DropSceneAssetCache from "../Utils/DropSceneAssetCache";
import GridStateBase from "./State/Base/GridStateBase";
import GridStateMachine from "./State/Base/GridStateMachine";
import GridStateCreate from "./State/Create/GridStateCreate";
import GridStateDrop from "./State/Drop/GridStateDrop";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Grid extends cc.Component {

    public static create(level: number, viewSize: cc.Size, cellSize: cc.Size, gridSize: cc.Size): Grid {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Grid/Grid");
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let grid = node.getComponent(Grid);
                if (grid) {
                    grid.init(level, viewSize, cellSize, gridSize)
                    return grid;
                }
            }
        }
        return null;
    }

    @property(cc.Node)
    layerRoot: cc.Node = null;

    private _origin: cc.Vec2 = cc.v2(0, 0);
    public getOrigin(): cc.Vec2 {
        return this._origin;
    }
    public setOrigin(pos: cc.Vec2) {
        this._origin = pos;
    }
    private _cellSize: cc.Size = cc.size(75, 75);
    public getCellSize(): cc.Size {
        return this._cellSize;
    }
    public setCellSize(size: cc.Size) {
        this._cellSize = size;
    }

    private _mapSize: cc.Size = cc.size(9, 12);
    public getMapSize(): cc.Size {
        return this._mapSize;
    }
    public setMapSize(size: cc.Size) {
        this._mapSize = size;
    }

    private _stateMachine: GridStateMachine = null;
    public getStateMachine(): GridStateMachine {
        return this._stateMachine;
    }

    public getCurrentState(): GridStateBase {
        return this._stateMachine.getCurrentState();
    }

    public setCurrentState(state: GridStateBase) {
        this._stateMachine.setCurrentState(state);
    }

    private _layers: { [key: string]: GridLayerBase } = {};
    public getLayers(): { [key: string]: GridLayerBase } {
        return this._layers;
    }

    public getPixelPositionWithLogicalPositon(logicalPosition: cc.Vec2): cc.Vec2 {
        return cc.v2(this._cellSize.width * logicalPosition.x, this._cellSize.height * logicalPosition.y).add(this._origin);
    }

    public getLogicalPositionWithPixelPositon(pixelPosition: cc.Vec2): cc.Vec2 {
        let column = Math.floor(pixelPosition.x / this._cellSize.width);
        let row = Math.floor(pixelPosition.y / this._cellSize.height);
        return cc.v2(column, row);
    }

    public getLayer(id: string): GridLayerBase {
        return this._layers[id];
    }

    public addLayer(layer: GridLayerBase) {
        layer.grid = this;
        this._layers[layer.id] = layer;
        this.layerRoot.addChild(layer.node);
    }

    public removeLayer(id: number): GridLayerBase {
        let layer = this._layers[id];
        if (layer) {
            layer.node.parent = null;
            layer.grid = null;
        }
        return layer;
    }

    public getUnit(id: string, logicalPosition: cc.Vec2): UnitBase {
        let layer = this.getLayer(id);
        if (layer) {
            return layer.getUnit(logicalPosition);
        }
        return null;
    }

    public setUnit(id: string, logicalPosition: cc.Vec2, unit: UnitBase): void {
        let layer = this.getLayer(id);
        if (layer) {
            layer.setUnit(logicalPosition, unit);
        }
    }

    public removeUnit(id: string, logicalPosition: cc.Vec2): UnitBase {
        let layer = this.getLayer(id);
        if (layer) {
            return layer.removeUnit(logicalPosition);
        }
        return null;
    }

    public onTouchStart(event: cc.Event.EventTouch) {
        let pos = this.layerRoot.convertToNodeSpaceAR(event.getLocation());
        this._stateMachine.onTouchStart(pos)
    }
    public onTouchMove(event: cc.Event.EventTouch) {
        let pos = this.layerRoot.convertToNodeSpaceAR(event.getLocation());
        this._stateMachine.onTouchMove(pos)
    }
    public onTouchEnd(event: cc.Event.EventTouch) {
        let pos = this.layerRoot.convertToNodeSpaceAR(event.getLocation());
        this._stateMachine.onTouchEnd(pos)
    }
    public onTouchCancel(event: cc.Event.EventTouch) {
        let pos = this.layerRoot.convertToNodeSpaceAR(event.getLocation());
        this._stateMachine.onTouchCancel(pos)
    }

    protected init(level: number, viewSize: cc.Size, cellSize: cc.Size, gridSize: cc.Size) {
        this._stateMachine = GridStateMachine.create(this);
        this.setCurrentState(GridStateCreate.create(level, viewSize, cellSize, gridSize))
    }

    public updateGrid(dt: number) {
        this._stateMachine.update(dt);
    }

    public clearSelectedCandy() {
        let mapSize = this.getMapSize();
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let candy: CandyBase = this.getUnit(LayerName.Candy, cc.v2(column, row)) as CandyBase;
                if (candy && candy.getSelected()) {
                    this.removeUnit(LayerName.Candy, cc.v2(column, row));
                }
            }
        }
        this.setCurrentState(GridStateDrop.create());
    }
}
