    protected getPositionCode(pos: cc.Vec2) {

        let bitOffsets: cc.Vec2[] = [
            cc.v2(1, 0),
            cc.v2(1, 1),
            cc.v2(0, 1),
            cc.v2(-1, 1),
            cc.v2(-1, 0),
            cc.v2(-1, -1),
            cc.v2(0, -1),
            cc.v2(1, -1),
        ];
        let code = 0;
        let bitValue = 1;

        for (let offset of bitOffsets) {
            let checkPos = pos.add(offset);
            let floor = this.grid.getUnit(LayerName.Floor, checkPos);
            if (floor) {
                code += bitValue;
            }
            bitValue *= 2;
        }
        return code;
    }

    protected init() {
        super.initFrame(UnitKind.FrameNormal);
    }
