import { LayerName } from "../../../Layer/Base/GridLayerBase";
import CandyFactory from "../../../Unit/Candy/CandyFactory";
import CandyStateBorn from "../../../Unit/Candy/Base/State/CandyStateBorn";
import Grid from "../../Grid";
import GridStateBase, { GridStateType } from "../Base/GridStateBase";
import FloorBase from "../../../Unit/Floor/Base/FloorBase";
import { UnitColor, UnitKind } from "../../../Unit/Base/UnitDefines";
import CandyBase from "../../../Unit/Candy/Base/CandyBase";



export default class GridGenerateLogic extends GridStateBase {
    public static create(): GridGenerateLogic {
        let instance = new GridGenerateLogic();
        instance.init()
        return instance;
    }
    protected init() {
        super.init(GridStateType.Drop)
    }
    private _generatedCandy: Set<CandyBase> = new Set();

    public enter(onwer: Grid): void {

    }

    public generateNewCandy(onwer: Grid): Set<CandyBase> {
        this._generatedCandy.clear();
        let size = onwer.getMapSize();
        for (let row = 0; row < size.height; row++) {
            for (let column = 0; column < size.width; column++) {
                let checkPos = cc.v2(column, row);
                let floor: FloorBase = onwer.getUnit(LayerName.Floor, checkPos) as FloorBase;
                if (floor && floor.isSpawn()) {
                    let candy: CandyBase = onwer.getUnit(LayerName.Candy, checkPos) as CandyBase;
                    if (candy == null) {
                        let colorCount = UnitColor.End - UnitColor.Begin;
                        let color = Math.floor(Math.random() * colorCount) + UnitColor.Begin
                        let param = { kind: UnitKind.CandyFruit, color: color }
                        candy = CandyFactory.createCandy(param);
                        onwer.setUnit(LayerName.Candy, checkPos, candy);
                        candy.setCurrentState(CandyStateBorn.create());
                        this._generatedCandy.add(candy);
                    }
                }
            }
        }
        return this._generatedCandy;
    }
}
