import UnitStateBase, { UnitStateType } from "../../../Base/State/UnitStateBase";
import CandyBase from "../CandyBase";


export default class CandyStateNormal extends UnitStateBase {
    public static create(): CandyStateNormal {
        let instance = new CandyStateNormal();
        instance.init();
        return instance;
    }

    protected init() {
        super.init(UnitStateType.Normal);
    }

    public enter(onwer: CandyBase): void {
        onwer.node.scale = 1.0;
    }

}
