
import { LayerName } from "../../../Layer/Base/GridLayerBase";
import CandyBase from "../../../Unit/Candy/Base/CandyBase";
import Grid from "../../Grid";
import GridStateBase, { GridStateType } from "../Base/GridStateBase";

export default class GridStateNormal extends GridStateBase {
    public static create(): GridStateNormal {
        let instance = new GridStateNormal();
        instance.init();
        return instance;
    }

    protected init() {
        super.init(GridStateType.Normal)
    }

    private _lastTouchPosition: cc.Vec2 = cc.v2(-1, -1);

    public onTouchStart(onwer: Grid, pos: cc.Vec2) {
        let logicalPos = onwer.getLogicalPositionWithPixelPositon(pos);
        let candy: CandyBase = onwer.getUnit(LayerName.Candy, logicalPos) as CandyBase
        if (candy) {
            candy.setSelected(!candy.getSelected());
        }
        this._lastTouchPosition = logicalPos;
    }
    public onTouchMove(onwer: Grid, pos: cc.Vec2) {

        let logicalPos = onwer.getLogicalPositionWithPixelPositon(pos);
        if (!logicalPos.equals(this._lastTouchPosition)) {
            let candy: CandyBase = onwer.getUnit(LayerName.Candy, logicalPos) as CandyBase
            if (candy) {
                candy.setSelected(!candy.getSelected());
            }
            this._lastTouchPosition = logicalPos;
        }

    }
    public onTouchEnd(onwer: Grid, pos: cc.Vec2) {
        this._lastTouchPosition = cc.v2(-1, -1);
    }
    public onTouchCancel(onwer: Grid, pos: cc.Vec2) {
        this._lastTouchPosition = cc.v2(-1, -1);
    }
}
