import UnitBase from "../../Base/UnitBase";
import { UnitCategory, UnitKind } from "../../Base/UnitDefines";
import CandyStateDrop from "./State/CandyStateDrop";
import CandyStateNormal from "./State/CandyStateNormal";
import CandyStateTeleport from "./State/CandyStateTeleport";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CandyBase extends UnitBase {

    public teleportToLogicalPosition(pos: cc.Vec2) {
        this.layer.moveUnit(this.getLogicalPostion(), pos, false);
        this.setCurrentState(CandyStateTeleport.create(this.grid.getPixelPositionWithLogicalPositon(this.getLogicalPostion())))
    }

    public moveToLogicalPosition(pos: cc.Vec2) {
        this.layer.moveUnit(this.getLogicalPostion(), pos, false);
        this.setCurrentState(CandyStateDrop.create(this.grid.getPixelPositionWithLogicalPositon(this.getLogicalPostion())))
    }

    public canDrop(): boolean {
        return true;
    }

    private _selected: boolean = false;
    public getSelected(): boolean {
        return this._selected;
    }
    public setSelected(selected: boolean) {
        this._selected = selected;
    }

    protected initCandy(kind: UnitKind) {
        super.initUnit(UnitCategory.Candy, kind);
        this.setCurrentState(CandyStateNormal.create());
        this.setSelected(false);
    }
}
