import UnitBase from "../../Base/UnitBase";
import { UnitCategory, UnitKind } from "../../Base/UnitDefines";
import FloorStateNormal from "./State/FloorStateNormal";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FloorBase extends UnitBase {

    private _isSpawn: boolean = false
    public isSpawn(): boolean {
        return this._isSpawn;
    }
    public setIsSpawn(isSpawn: boolean) {
        this._isSpawn = isSpawn;
    }

    protected initFloor(kind: UnitKind) {
        super.initUnit(UnitCategory.Floor, kind);
        this.setCurrentState(FloorStateNormal.create());
    }
}