import { LayerName } from "../../../Layer/Base/GridLayerBase";
import CandyBase from "../../../Unit/Candy/Base/CandyBase";
import Grid from "../../Grid";
import GridStateBase, { GridStateType } from "../Base/GridStateBase";
import FloorBase from "../../../Unit/Floor/Base/FloorBase";
import DropSceneMath from "../../../Utils/DropSceneMath";
import { UnitKind } from "../../../Unit/Base/UnitDefines";
import FloorExit from "../../../Unit/Floor/Exit/FloorExit";
import FloorEntrance from "../../../Unit/Floor/Entrance/FloorEntrance";



export default class GridDropLogic extends GridStateBase {

    public static create(): GridDropLogic {
        let instance = new GridDropLogic();
        instance.init()
        return instance;
    }

    private _searchOffsets: cc.Vec2[] = [];
    protected init() {
        super.init(GridStateType.Drop)
        this._floorStatusDict = {};
        this._wallsDict = {};
        this._searchOffsets.push(cc.v2(0, 1));
        this._searchOffsets.push(cc.v2(-1, 1));
        this._searchOffsets.push(cc.v2(1, 1));
    }


    private _floorStatusDict: { [key: number]: boolean } = {}
    /**
     *  
     * @param onwer 
     * @param logicalPosition 
     */
    private getFloorStatus(onwer: Grid, logicalPosition: cc.Vec2, checkedFloor: Set<number> = null) {
        if (checkedFloor == null) {
            checkedFloor = new Set<number>();
        }
        let key = DropSceneMath.getKeyWithLogicalPosition(logicalPosition);
        if (checkedFloor.has(key)) {
            return false;
        }
        checkedFloor.add(key);
        // 
        let status = this._floorStatusDict[key];
        if (status != null) {
            return status;
        }

        let floor: FloorBase = onwer.getUnit(LayerName.Floor, logicalPosition) as FloorBase;
        if (floor == null) {
            // 
            this._floorStatusDict[key] = false;
            return false;
        }

        let candy: CandyBase = onwer.getUnit(LayerName.Candy, logicalPosition) as CandyBase;
        if (candy && !candy.canDrop()) {
            // 
            this._floorStatusDict[key] = false;
            return false;
        }

        if (floor.isSpawn()) {
            // 
            this._floorStatusDict[key] = true;
            return true;
        }

        if (floor.getUnitKind() == UnitKind.FloorExit) {
            // 
            let exit: FloorExit = floor as FloorExit;
            let status = this.getFloorStatus(onwer, exit.getEntrance(), checkedFloor);
            if (status) {
                this._floorStatusDict[key] = true;
                return true;
            }
        }

        let size = onwer.getMapSize();
        for (const offset of this._searchOffsets) {
            let checkPos = logicalPosition.add(offset);
            if (checkPos.x >= 0 && checkPos.x < size.width && checkPos.y >= 0 && checkPos.y < size.height) {
                if (this.hasWall(logicalPosition, checkPos) == false) {
                    let status = this.getFloorStatus(onwer, checkPos, checkedFloor);
                    if (status) {
                        // 
                        this._floorStatusDict[key] = true;
                        return true;
                    }
                }
            }
        }
        // 
        this._floorStatusDict[key] = false;
        return false;
    }

    /**
     * 
     * @param onwer 
     */
    private recordFloorStatus(onwer: Grid) {
        let size = onwer.getMapSize();
        for (let row = size.height - 1; row > -1; row--) {
            for (let column = 0; column < size.width; column++) {
                let checkPos = cc.v2(column, row);
                this.getFloorStatus(onwer, checkPos);
            }
        }
    }

    private _wallsDict: { [key: string]: boolean } = {};
    private getWallKeyWithLogicalPosition(from: cc.Vec2, to: cc.Vec2): string {
        let key = cc.js.formatStr("{%s,%s}->{%s,%s}", from.x, from.y, to.x, to.y);
        return key;
    }
    private hasWall(from: cc.Vec2, to: cc.Vec2): boolean {
        let key = this.getWallKeyWithLogicalPosition(from, to);
        let value = this._wallsDict[key];
        if (value != null) {
            return true;
        }
        return false;
    }

    private recordWalls(onwer: Grid) {
        let size = onwer.getMapSize();
        for (let row = 0; row < size.height; row++) {
            for (let column = 0; column < size.width; column++) {
                let checkPos = cc.v2(column, row);
                let floor: FloorBase = onwer.getUnit(LayerName.Floor, checkPos) as FloorBase;
                if (floor.getUnitKind() == UnitKind.FloorEntrance) {
                    let downKey = this.getWallKeyWithLogicalPosition(checkPos, checkPos.add(cc.v2(0, -1)))
                    this._wallsDict[downKey] = true;
                    let upKey = this.getWallKeyWithLogicalPosition(checkPos.add(cc.v2(0, -1)), checkPos)
                    this._wallsDict[upKey] = true;
                }

                if (floor.getUnitKind() == UnitKind.FloorExit) {
                    let downKey = this.getWallKeyWithLogicalPosition(checkPos, checkPos.add(cc.v2(0, 1)))
                    this._wallsDict[downKey] = true;
                    let upKey = this.getWallKeyWithLogicalPosition(checkPos.add(cc.v2(0, 1)), checkPos)
                    this._wallsDict[upKey] = true;
                }
            }
        }
    }

    private isFloorAvailable(onwer: Grid, logicalPosition: cc.Vec2): boolean {
        let floor = onwer.getUnit(LayerName.Floor, logicalPosition)
        if (floor == null) {
            return false;
        }

        let candy = onwer.getUnit(LayerName.Candy, logicalPosition)
        if (candy != null) {
            return false
        }

        return true;
    }

    /**
     * 
     * @param onwer 
     * @param checkPos 
     */
    private dropCandy(onwer: Grid, checkPos: cc.Vec2, checkedCandy: Set<CandyBase>, movedCandy: Set<CandyBase>): boolean {
        let candy: CandyBase = onwer.getUnit(LayerName.Candy, checkPos) as CandyBase;
        if (candy && !checkedCandy.has(candy)) {
            checkedCandy.add(candy);
            if (candy.canDrop()) {
                // 
                let floor: FloorBase = onwer.getUnit(LayerName.Floor, checkPos) as FloorBase;
                if (floor.getUnitKind() == UnitKind.FloorEntrance) {
                    let entrance: FloorEntrance = floor as FloorEntrance;
                    let eixtPos = entrance.getExit()
                    this.dropCandy(onwer, eixtPos, checkedCandy, movedCandy)
                    if (this.isFloorAvailable(onwer, eixtPos)) {
                        candy.teleportToLogicalPosition(eixtPos);
                        movedCandy.add(candy);
                        return true;
                    }
                }

                // 
                let downPos = checkPos.add(cc.v2(0, -1))
                if (this.hasWall(checkPos, downPos) == false) {
                    this.dropCandy(onwer, downPos, checkedCandy, movedCandy)
                    if (this.isFloorAvailable(onwer, downPos)) {
                        candy.moveToLogicalPosition(downPos);
                        movedCandy.add(candy);
                        return true;
                    }
                }

                // 
                let leftDownPos = checkPos.add(cc.v2(-1, -1));
                if (this.hasWall(checkPos, leftDownPos) == false) {
                    // 
                    let leftPos = checkPos.add(cc.v2(-1, 0));
                    if (this.hasWall(leftPos, leftDownPos) || this.getFloorStatus(onwer, leftPos) == false) {
                        this.dropCandy(onwer, leftDownPos, checkedCandy, movedCandy);
                        if (this.isFloorAvailable(onwer, leftDownPos)) {
                            candy.moveToLogicalPosition(leftDownPos);
                            movedCandy.add(candy);
                            return true;
                        }
                    }
                }

                // 
                let rightDownPos = checkPos.add(cc.v2(1, -1));
                if (this.hasWall(checkPos, rightDownPos) == false) {
                    let rightPos = checkPos.add(cc.v2(1, 0));
                    // 
                    if (this.hasWall(rightPos, rightDownPos) || this.getFloorStatus(onwer, rightPos) == false) {
                        this.dropCandy(onwer, rightDownPos, checkedCandy, movedCandy);
                        if (this.isFloorAvailable(onwer, rightDownPos)) {
                            candy.moveToLogicalPosition(rightDownPos);
                            movedCandy.add(candy);
                            return true;
                        }
                    }
                }
            }
        }
        return false;
    }

    /**
     * 
     * @param onwer 
     * @param checkedCandy 
     * @param movedCandy 
     */
    public tryDropAllCandy(onwer: Grid, checkedCandy: Set<CandyBase> = null, movedCandy: Set<CandyBase> = null): Set<CandyBase> {
        if (checkedCandy == null) {
            checkedCandy = new Set<CandyBase>();
        }
        if (movedCandy == null) {
            movedCandy = new Set<CandyBase>();
        }
        let size = onwer.getMapSize();
        for (let row = 0; row < size.height; row++) {
            for (let column = 0; column < size.width; column++) {
                let checkPos = cc.v2(column, row);
                this.dropCandy(onwer, checkPos, checkedCandy, movedCandy)
            }
        }
        return movedCandy;
    }

    public enter(onwer: Grid): void {
        this.recordFloorStatus(onwer);
        this.recordWalls(onwer);
    }

    public onTouchStart(onwer: Grid, pos: cc.Vec2) {
        let logicalPos = onwer.getLogicalPositionWithPixelPositon(pos);
        let status = this.getFloorStatus(onwer, logicalPos);
        console.log(cc.js.formatStr("GridDropLogic.onTouchStart Pos{%s,%s} is %s", logicalPos.x, logicalPos.y, status ? "Alive" : "Dead"))
    }
}
