

import DropSceneAssetCache from "../../../Utils/DropSceneAssetCache";
import { UnitKind } from "../../Base/UnitDefines";
import FloorBase from "../Base/FloorBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FloorFixed extends FloorBase {

    public static create(): FloorFixed {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Unit/Floor/Fixed/FloorFixed");
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let item = node.getComponent(FloorFixed);
                if (item) {
                    item.init();
                    return item;
                }
            }
        }
        return null;
    }

    @property(cc.Sprite)
    doll: cc.Sprite = null;

    protected init() {
        super.initFloor(UnitKind.FloorFixed);
    }

}
