import SimpleStateMachine from "../../../../../../../scripts/Frameworks/EWS/FSM/SimpleStateMachine";
import Grid from "../../Grid";
import GridStateBase from "./GridStateBase";


export default class GridStateMachine extends SimpleStateMachine<Grid> {
    public static create(onwer: Grid) {
        let instance = new GridStateMachine(onwer);
        return instance;
    }

    public getCurrentState(): GridStateBase {
        return super.getCurrentState() as GridStateBase;
    }

    public onTouchStart(pos: cc.Vec2) {
        let currentState = this.getCurrentState();
        if (currentState) {
            currentState.onTouchStart(this.getOnwer(), pos);
        }
    }
    public onTouchMove(pos: cc.Vec2) {
        let currentState = this.getCurrentState();
        if (currentState) {
            currentState.onTouchMove(this.getOnwer(), pos);
        }
    }
    public onTouchEnd(pos: cc.Vec2) {
        let currentState = this.getCurrentState();
        if (currentState) {
            currentState.onTouchEnd(this.getOnwer(), pos);
        }
    }
    public onTouchCancel(pos: cc.Vec2) {
        let currentState = this.getCurrentState();
        if (currentState) {
            currentState.onTouchCancel(this.getOnwer(), pos);
        }
    }
}
