import Grid from "../../Grid/Grid";
import UnitBase from "../../Unit/Base/UnitBase";
import DropSceneAssetCache from "../../Utils/DropSceneAssetCache";


const { ccclass, property } = cc._decorator;

export enum LayerName {
    None = "none",
    Frame = "frame",
    Floor = "floor",
    Candy = "candy",
}

@ccclass
export default abstract class GridLayerBase extends cc.Component {

    private _grid: Grid = null;
    public get grid(): Grid {
        return this._grid;
    }
    public set grid(grid: Grid) {
        this._grid = grid;
    }

    private _size: cc.Size = cc.size(0, 0);
    public get size(): cc.Size {
        return this._size;
    }
    public set size(size: cc.Size) {
        this._size = size;
    }

    private _id: string = "";
    public get id(): string {
        return this._id;
    }
    public set id(id: string) {
        this._id = id;
    }

    private _unitDict: { [key: string]: UnitBase } = {};

    public getUnit(logicalPos: cc.Vec2): UnitBase {
        let key = this.getKeyWithLogicalPosition(logicalPos)
        return this._unitDict[key];
    }
    public setUnit(logicalPos: cc.Vec2, unit: UnitBase): UnitBase {
        let key = this.getKeyWithLogicalPosition(logicalPos)
        let removedUnit = this.removeUnit(logicalPos);
        unit.layer = this;
        unit.setPixelPosition(this.grid.getPixelPositionWithLogicalPositon(logicalPos));
        unit.setLogicalPostion(logicalPos);
        this._unitDict[key] = unit;
        this.node.addChild(unit.node)
        return removedUnit;
    }

    public moveUnit(from: cc.Vec2, to: cc.Vec2, syncPixelPos: boolean = true) {
        let fromKey = this.getKeyWithLogicalPosition(from)
        let toKey = this.getKeyWithLogicalPosition(to)
        if (this._unitDict[fromKey] && this._unitDict[toKey] == null) {
            this._unitDict[toKey] = this._unitDict[fromKey];
            this._unitDict[fromKey] = null;
            this._unitDict[toKey].setLogicalPostion(to);
            if (syncPixelPos) {
                this._unitDict[toKey].setPixelPosition(this.grid.getPixelPositionWithLogicalPositon(to));
            }
        }
    }

    public removeUnit(logicalPos: cc.Vec2, cleanup: boolean = false): UnitBase {
        let key = this.getKeyWithLogicalPosition(logicalPos)
        return this.removeUnitWithKey(key, cleanup);
    }

    public removeUnitWithKey(key: number, cleanup: boolean = false): UnitBase {
        let unit = this._unitDict[key];
        if (unit) {
            unit.layer = null;
            unit.node.removeFromParent(cleanup);
            delete this._unitDict[key];
        }
        return unit;
    }

    private getKeyWithLogicalPosition(logicalPosition: cc.Vec2): number {
        return DropSceneAssetCache.getKeyWithLogicalPosition(logicalPosition)
    }

    protected init(type: LayerName, contentSize: cc.Size, gridSize: cc.Size) {
        this.id = type;
        this.node.setAnchorPoint(0, 0);
        this.node.setContentSize(contentSize);
        this.size = gridSize;
    }

    public updateLayer(dt: number) {
        for (let row = 0; row < this.size.height; row++) {
            for (let column = 0; column < this.size.width; column++) {
                let unit = this.getUnit(cc.v2(column, row));
                if (unit) {
                    unit.updateUnit(dt);
                }
            }
        }
    }
}
