

import DropSceneAssetCache from "../../../Utils/DropSceneAssetCache";
import { UnitKind } from "../../Base/UnitDefines";
import FloorBase from "../Base/FloorBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FloorExit extends FloorBase {

    public static create(exit: cc.Vec2): FloorExit {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Unit/Floor/Exit/FloorExit");
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let item = node.getComponent(FloorExit);
                if (item) {
                    item.initWithEntrance(exit);
                    return item;
                }
            }
        }
        return null;
    }

    @property(cc.Sprite)
    doll: cc.Sprite = null;


    private _entrance: cc.Vec2 = cc.v2(0, 0);
    public getEntrance(): cc.Vec2 {
        return this._entrance;
    }
    protected setEntrance(pos: cc.Vec2) {
        this._entrance = pos;
    }

    protected initWithEntrance(exit: cc.Vec2) {
        super.initFloor(UnitKind.FloorExit);
        this.setEntrance(exit);
        this.node.zIndex = 1;
    }

}
