import { AssetBundleID, SceneName } from "../../../scripts/Defines/Defines";
import IGame from "../../../scripts/Frameworks/IGame/IGame";
import PreloadBundleInfo from "../../../scripts/Frameworks/SceneDirector/PreloadAssetInfo";
import SceneBase from "../../../scripts/Frameworks/SceneDirector/SceneBase";
import SceneEnterParamBase from "../../../scripts/Frameworks/SceneDirector/SceneEnterParamBase";
import Grid from "./Game/Grid/Grid";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DropScene extends SceneBase {


    public getSceneName(): string {
        return SceneName.DropScene;
    }

    @property(cc.Node)
    gridContainer: cc.Node = null;


    private _grid: Grid = null;
    // LIFE-CYCLE CALLBACKS:
    public onSceneLoad(): void {

    }
    public onSceneEnable(): void {

    }
    public onSceneEnter(param: SceneEnterParamBase): void {

    }
    public onSceneStart(): void {
        this._grid = Grid.create(1, this.gridContainer.getContentSize(), cc.size(75, 75), cc.size(9, 12));
        this.gridContainer.addChild(this._grid.node);

        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchCancel, this);
    }
    public onSceneUpdate(dt: number): void {
        this._grid && this._grid.updateGrid(dt);
    }
    public onSceneExit(): void {

    }
    public onSceneDisable(): void {

    }
    public onSceneDestroy(): void {

    }

    private onStepBtn(event: cc.Event.EventTouch) {
        this._grid && this._grid.clearSelectedCandy();
    }

    private onQuitBtn(event: cc.Event.EventTouch) {
        let preloadInfo = PreloadBundleInfo.create();
        preloadInfo.extiBundles = [AssetBundleID.DropScene];
        preloadInfo.enterBundles[AssetBundleID.MainScene] = [];
        IGame.getInstance().replaceSceneWithSwitchScene(preloadInfo, SceneName.MainScene);
    }

    private onTouchStart(event: cc.Event.EventTouch) {
        this._grid && this._grid.onTouchStart(event);
    }
    private onTouchMove(event: cc.Event.EventTouch) {
        this._grid && this._grid.onTouchMove(event);
    }
    private onTouchEnd(event: cc.Event.EventTouch) {
        this._grid && this._grid.onTouchEnd(event);
    }
    private onTouchCancel(event: cc.Event.EventTouch) {
        this._grid && this._grid.onTouchCancel(event);
    }

}
