

import DropSceneAssetCache from "../../../Utils/DropSceneAssetCache";
import { UnitKind } from "../../Base/UnitDefines";
import CandyBase from "../Base/CandyBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CandyCake extends CandyBase {
    public static create(hp: number): CandyCake {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Unit/Candy/Cake/CandyCake")
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let item = node.getComponent(CandyCake);
                if (item) {
                    item.initWithHealthPoint(hp);
                    return item;
                }
            }
        }
        return null;
    }

    @property(cc.Sprite)
    doll: cc.Sprite = null;

    @property(cc.Node)
    mark: cc.Node = null;

    public canDrop(): boolean {
        return false;
    }

    public setHp(hp: number) {
        hp = Math.min(4, Math.max(1, hp));
        super.setHp(hp);
        let filename = "texture/Game/Candy/Cake/6080" + this.getHP().toString();
        this.doll.spriteFrame = DropSceneAssetCache.getSpriteFrame(filename);
    }

    public setSelected(selected: boolean) {
        super.setSelected(selected);
        this.mark.active = this.getSelected();
    }

    protected initWithHealthPoint(hp: number) {
        super.initCandy(UnitKind.CandyCake);
        this.setHp(hp);
    }

}
