
import DropSceneAssetCache from "../../../Utils/DropSceneAssetCache";
import { UnitColor, UnitKind } from "../../Base/UnitDefines";
import CandyBase from "../Base/CandyBase";




const { ccclass, property } = cc._decorator;

@ccclass
export default class CandyFruit extends CandyBase {

    public static create(color: UnitColor): CandyFruit {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Unit/Candy/Fruit/CandyFruit");
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let item = node.getComponent(CandyFruit);
                if (item) {
                    item.initWithColor(color);
                    return item;
                }
            }
        }
        return null;
    }

    @property(cc.Sprite)
    doll: cc.Sprite = null;

    @property(cc.Node)
    mark: cc.Node = null;

    public canDrop(): boolean {
        return true;
    }

    public setColor(color: UnitColor) {
        super.setColor(color);
        let filename = "texture/Game/Candy/Fruit/6010" + color.toString();
        this.doll.spriteFrame = DropSceneAssetCache.getSpriteFrame(filename);
    }

    public setSelected(selected: boolean) {
        super.setSelected(selected);
        this.mark.active = this.getSelected();
    }

    protected initWithColor(color: UnitColor) {
        super.initCandy(UnitKind.CandyFruit);
        this.setColor(color);
    }


}
