

import SimpleStateMachine from "../../../../../../../scripts/Frameworks/EWS/FSM/SimpleStateMachine";
import UnitBase from "../UnitBase";
import UnitStateBase from "./UnitStateBase";



const { ccclass, property } = cc._decorator;

@ccclass
export default class UnitStateMachine extends SimpleStateMachine<UnitBase> {

    public static create(onwer: UnitBase) {
        let instance = new UnitStateMachine(onwer);
        return instance;
    }

    public getCurrentState(): UnitStateBase {
        return super.getCurrentState() as UnitStateBase;
    }

    public setCurrentState(newState: UnitStateBase): void {
        super.setCurrentState(newState);
    }
}
