
import { LayerName } from "../../../Layer/Base/GridLayerBase";
import { UnitStateType } from "../../../Unit/Base/State/UnitStateBase";
import Grid from "../../Grid";
import GridStateBase, { GridStateType } from "../Base/GridStateBase";
import GridStateNormal from "../Normal/GridStateNormal";
import GridDropLogic from "./GridDropLogic";
import GridGenerateLogic from "./GridGenerateLogic";


export default class GridStateDrop extends GridStateBase {
    public static create(): GridStateDrop {
        let instance = new GridStateDrop();
        instance.init();
        return instance;
    }

    private _dropLogical: GridDropLogic = null;
    private _generateLogic: GridGenerateLogic = null;

    protected init() {
        super.init(GridStateType.Drop)
        this._dropLogical = GridDropLogic.create();
        this._generateLogic = GridGenerateLogic.create();
    }

    public enter(onwer: Grid): void {
        this._dropLogical.enter(onwer);
        this._generateLogic.enter(onwer);
    }

    private _steps = 0;
    public update(onwer: Grid, dt: number): void {
        super.update(onwer, dt);
        let mapSize = onwer.getMapSize();
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let unit = onwer.getUnit(LayerName.Candy, cc.v2(column, row));
                if (unit && unit.getCurrentState().getType() != UnitStateType.Normal) {
                    return
                }
            }
        }


        // if (this._steps > 0) {
        //     onwer.setCurrentState(GridStateNormal.create());
        //     return;
        // }
        let dropedCandy = this._dropLogical.tryDropAllCandy(onwer);
        let generatedCandy = this._generateLogic.generateNewCandy(onwer);
        this._steps++
        if (dropedCandy.size == 0 && generatedCandy.size == 0) {
            onwer.setCurrentState(GridStateNormal.create());
        }
    }

    public onTouchStart(onwer: Grid, pos: cc.Vec2) {
        this._dropLogical.onTouchStart(onwer, pos);
    }

}
