
import { UnitColor, UnitKind } from "../Base/UnitDefines";
import CandyBase from "./Base/CandyBase";
import CandyCake from "./Cake/CandyCake";
import CandyFruit from "./Fruit/CandyFruit";

export default class CandyFactory {

    public static createCandy(param: { [key: string]: any }): CandyBase {
        let kind = param["kind"];
        switch (kind) {
            case UnitKind.CandyFruit: {
                let color: number = param["color"] || UnitColor.Red;
                return CandyFruit.create(color);
            }
                break;
            case UnitKind.CandyCake: {
                let hp: number = param["healthPoint"] || 1;
                return CandyCake.create(hp);
            }
                break;
            default:
                break;
        }
        return null;
    }
}
