

import DropSceneAssetCache from "../../../Utils/DropSceneAssetCache";
import { UnitKind } from "../../Base/UnitDefines";
import FloorBase from "../Base/FloorBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FloorEntrance extends FloorBase {

    public static create(exit: cc.Vec2): FloorEntrance {
        let res: cc.Prefab = DropSceneAssetCache.getPrefab("prefab/Game/Unit/Floor/Entrance/FloorEntrance");
        if (res) {
            let node: cc.Node = cc.instantiate(res);
            if (node) {
                let item = node.getComponent(FloorEntrance);
                if (item) {
                    item.initWithExit(exit);
                    return item;
                }
            }
        }
        return null;
    }

    @property(cc.Sprite)
    doll: cc.Sprite = null;


    private _exit: cc.Vec2 = cc.v2(0, 0);
    public getExit(): cc.Vec2 {
        return this._exit;
    }
    protected setExit(pos: cc.Vec2) {
        this._exit = pos;
    }

    protected initWithExit(exit: cc.Vec2) {
        super.initFloor(UnitKind.FloorEntrance);
        this.setExit(exit);
        this.node.zIndex = 1;
    }

}
