import Message from "../../../../../../../scripts/Frameworks/EWS/FSM/Message";
import State from "../../../../../../../scripts/Frameworks/EWS/FSM/State";
import Grid from "../../Grid";

export enum GridStateType {
    None = 0,
    Create,
    Normal,
    Drop
}

export default class GridStateBase extends State<Grid> {

    private _type: GridStateType = GridStateType.None;
    public getType(): GridStateType {
        return this._type;
    }

    protected init(type: GridStateType) {
        this._type = type;
    }

    public enter(onwer: Grid): void {

    }
    public update(onwer: Grid, dt: number): void {
        let layerDict = onwer.getLayers();
        for (const key in layerDict) {
            let layer = layerDict[key];
            layer.updateLayer(dt);
        }
    }
    public exit(onwer: Grid): void {

    }
    public onMessage(message: Message): boolean {
        return false;
    }
    public reset(): void {

    }

    public onTouchStart(onwer: Grid, pos: cc.Vec2) {

    }
    public onTouchMove(onwer: Grid, pos: cc.Vec2) {

    }
    public onTouchEnd(onwer: Grid, pos: cc.Vec2) {

    }
    public onTouchCancel(onwer: Grid, pos: cc.Vec2) {

    }

}
