import { AssetBundleID } from "../../../../../../../scripts/Defines/Defines";
import TiledMapJsonPhraser from "../../../../../../../scripts/Frameworks/TiledMap/TiledMapJsonPhraser";
import GameMath from "../../../../../../../scripts/Frameworks/Utils/GameMath";
import { LayerName } from "../../../Layer/Base/GridLayerBase";
import FloorLayer from "../../../Layer/FloorLayer/FloorLayer";
import FrameLayer from "../../../Layer/FrameLayer/FrameLayer";
import CandyLayer from "../../../Layer/CandyLayer/CandyLayer";
import FloorFactory from "../../../Unit/Floor/FloorFactory";
import CandyFactory from "../../../Unit/Candy/CandyFactory";
import Grid from "../../Grid";
import GridStateBase, { GridStateType } from "../Base/GridStateBase";
import GridStateNormal from "../Normal/GridStateNormal";
import FloorBase from "../../../Unit/Floor/Base/FloorBase";
import FrameFactory from "../../../Unit/Frame/FrameFactory";
import DropSceneMath from "../../../Utils/DropSceneMath";
import { UnitKind } from "../../../Unit/Base/UnitDefines";
import GridStateDrop from "../Drop/GridStateDrop";


export default class GridStateCreate extends GridStateBase {

    public static create(level: number, viewSize: cc.Size, cellSize: cc.Size, gridSize: cc.Size): GridStateCreate {
        let instance = new GridStateCreate();
        instance.initWithLevel(level, viewSize, cellSize, gridSize);
        return instance;
    }

    private _level: number = 0;
    private _viewSize: cc.Size = cc.size(0, 0);
    private _cellSize: cc.Size = cc.size(0, 0);
    private _mapSize: cc.Size = cc.size(0, 0);
    protected initWithLevel(level: number, viewSize: cc.Size, cellSize: cc.Size, gridSize: cc.Size) {
        super.init(GridStateType.Create)
        this._level = level;
        this._viewSize = viewSize;
        this._cellSize = cellSize;
        this._mapSize = gridSize;
    }

    public enter(onwer: Grid): void {

        onwer.setCellSize(this._cellSize);
        onwer.setMapSize(this._mapSize);

        onwer.setOrigin(cc.v2(this._cellSize.width * 0.5, this._cellSize.height * 0.5));
        let contentSize = cc.size(
            this._cellSize.width * this._mapSize.width,
            this._cellSize.height * this._mapSize.height)
        onwer.node.setContentSize(contentSize);
        let scale = GameMath.getScaleFromViewSize(this._viewSize,
            cc.size(contentSize.width + this._cellSize.width, contentSize.height + this._cellSize.height),
            0.75, 1.25);
        onwer.node.scale = scale;

        onwer.addLayer(FrameLayer.create(contentSize, this._mapSize));
        onwer.addLayer(FloorLayer.create(contentSize, this._mapSize));
        onwer.addLayer(CandyLayer.create(contentSize, this._mapSize));

        let tmx = TiledMapJsonPhraser.createWithFile(AssetBundleID.DropScene, "config/level" + this._level.toString());
        this.createFloor(tmx, onwer)
        this.createCandy(tmx, onwer)
        this.createFrame(tmx, onwer);

        onwer.setCurrentState(GridStateDrop.create());
    }

    private createFloor(tmx: TiledMapJsonPhraser, onwer: Grid) {
        let teleporterGruop: { [key: number]: { entrance: cc.Vec2, exit: cc.Vec2 } } = {};

        let mapSize = tmx.getMapSize();
        for (let column = 0; column < mapSize.width; column++) {
            for (let row = 0; row < mapSize.height; row++) {
                let checkPos = cc.v2(column, mapSize.height - 1 - row)
                let markParams: { [key: string]: any } = {}
                let markProperties = tmx.getObjectInfos("Mark", checkPos);
                if (markProperties) {
                    for (const properties of markProperties) {
                        let tilesetProperty = tmx.getTilesetPropertyWithGID(properties.gid);
                        for (const key in tilesetProperty.propertyDict) {
                            markParams[key] = tilesetProperty.propertyDict[key].value;
                        }
                        for (const key in properties.propertyDict) {
                            markParams[key] = properties.propertyDict[key].value;
                        }
                    }

                    let kind = markParams["kind"]
                    if (kind == UnitKind.MarkEntrance || kind == UnitKind.MarkExit) {
                        let group = markParams["group"];
                        if (teleporterGruop[group] == null) {
                            teleporterGruop[group] = { entrance: cc.v2(-1, -1), exit: cc.v2(-1, -1) };
                        }
                        if (kind == UnitKind.MarkEntrance) {
                            teleporterGruop[group].entrance = cc.v2(column, row);
                        }
                        if (kind == UnitKind.MarkExit) {
                            teleporterGruop[group].exit = cc.v2(column, row);
                        }
                    }
                }
            }
        }

        let teleporters: { [key: number]: { kind: number, relatedPosition: cc.Vec2 } } = {}

        for (const key in teleporterGruop) {
            const value = teleporterGruop[key];
            let entrance = { kind: UnitKind.FloorEntrance, relatedPosition: value.exit };
            let entranceKey = DropSceneMath.getKeyWithLogicalPosition(value.entrance);
            teleporters[entranceKey] = entrance;

            let exit = { kind: UnitKind.FloorExit, relatedPosition: value.entrance };
            let exitKey = DropSceneMath.getKeyWithLogicalPosition(value.exit);
            teleporters[exitKey] = exit;
        }


        for (let column = 0; column < mapSize.width; column++) {
            let topFloor: FloorBase = null
            for (let row = 0; row < mapSize.height; row++) {
                let checkPos = cc.v2(column, mapSize.height - 1 - row)
                let params: { [key: string]: any } = {}
                let floorProperties = tmx.getTilesetProperty("Floor", checkPos);
                if (floorProperties) {
                    for (const key in floorProperties.propertyDict) {
                        params[key] = floorProperties.propertyDict[key].value;
                    }
                }

                let key = DropSceneMath.getKeyWithLogicalPosition(cc.v2(column, row));
                let teleporterProperties = teleporters[key];
                if (teleporterProperties) {
                    for (const key in teleporterProperties) {
                        params[key] = teleporterProperties[key];
                    }
                }

                let floor = FloorFactory.createFloor(params)
                if (floor) {
                    onwer.setUnit(LayerName.Floor, cc.v2(column, row), floor);
                    topFloor = floor;
                }
            }
            if (topFloor) {
                topFloor.setIsSpawn(true);
            }
        }
    }

    private createCandy(tmx: TiledMapJsonPhraser, onwer: Grid) {
        let mapSize = tmx.getMapSize();
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let params: { [key: string]: any } = {}
                let checkPos = cc.v2(column, mapSize.height - 1 - row)
                let properties = tmx.getTilesetProperty("Candy", checkPos);
                if (properties) {
                    for (const key in properties.propertyDict) {
                        params[key] = properties.propertyDict[key].value;
                    }
                }
                let candy = CandyFactory.createCandy(params);
                if (candy) {
                    onwer.setUnit(LayerName.Candy, cc.v2(column, row), candy);
                }
            }
        }
    }

    private createFrame(tmx: TiledMapJsonPhraser, onwer: Grid) {
        let mapSize = tmx.getMapSize();
        for (let row = -1; row <= mapSize.height; row++) {
            for (let column = -1; column <= mapSize.width; column++) {
                let floor = onwer.getUnit(LayerName.Floor, cc.v2(column, row))
                if (floor == null) {
                    let params: { [key: string]: any } = { kind: UnitKind.FrameNormal }
                    let frame = FrameFactory.createFrame(params);
                    onwer.setUnit(LayerName.Frame, cc.v2(column, row), frame);
                }
            }
        }
    }
}
